import { Dispatch, SetStateAction } from "react"
import {
  SourceSelectorSelectedCategoryInfo,
  SourceSelectorSelectedSourceInfoV2,
} from "../sourceSelector.types"
import { getIsSourceSelection } from "./getIsSourceSelection"

type removeSelectedItemFuncProps = {
  item: SourceSelectorSelectedSourceInfoV2 | SourceSelectorSelectedCategoryInfo
  selectedSources: Array<SourceSelectorSelectedSourceInfoV2>
  setSelectedSources: Dispatch<SetStateAction<Array<SourceSelectorSelectedSourceInfoV2>>>
  selectedCategories: Array<SourceSelectorSelectedCategoryInfo>
  setSelectedCategories: Dispatch<SetStateAction<Array<SourceSelectorSelectedCategoryInfo>>>
}

function removeSelectedItemFunc({
  item,
  selectedSources,
  setSelectedSources,
  selectedCategories,
  setSelectedCategories,
}: removeSelectedItemFuncProps): void {
  const isSourceSelection = getIsSourceSelection(item)

  const list = isSourceSelection ? selectedSources : selectedCategories
  const idx = list.findIndex(source => source.id === item.id)

  if (idx === -1) return

  if (isSourceSelection) {
    setSelectedSources(selectedSources => [
      ...selectedSources.slice(0, idx),
      ...selectedSources.slice(idx + 1),
    ])
  } else {
    setSelectedCategories(selectedCategories => [
      ...selectedCategories.slice(0, idx),
      ...selectedCategories.slice(idx + 1),
    ])
  }
}

export { removeSelectedItemFunc }
